<template>
    <div class="page">
        <tab></tab>
        <sort></sort>
        <goods :goodslist="goodsList"></goods>
        <infinite-loading @infinite="infiniteHandler"></infinite-loading>

        <!-- <nice-title :type="1">9kuai9</nice-title> -->
        <tab-bar></tab-bar>
        <!-- <div class="bottom-tip" v-show="nodata">暂无数据</div> -->
    </div>
</template>

<script type="text/ecmascript-6">
import TabBar from '../tabBar/TabBar'
import axios from 'axios'
import InfiniteLoading from 'vue-infinite-loading'
import NiceTitle from 'common/nicetitle/NiceTitle'
import Goods from 'common/goods/Goods'
import Sort from 'common/sort/Sort'
import Tab from 'common/tab/Tab'
import Bus from '@/assets/js/eventBus'
export default {
    name: 'kuai9',
    data () {
        return {
            sortnumber: 1,
            cat: 0,
            goodsList: [],
            page: 1,
            nodata: false,
            total: ''
        }
    },
    watch: {

    },
    created () {
        // this.getGoods()
        /* 获取子组件的selectType的更新 */
        Bus.$on('sortnumber', sortnumber => {
            if (sortnumber !== this.sortnumber) {
                this.sortnumber = sortnumber
                this.page = 1
                this.nodata = false
                this.goodsList = []
                this.getGoods()
            }
        })
        Bus.$on('cat', cat => {
            if (cat !== this.cat) {
                this.cat = cat
                this.page = 1
                this.nodata = false
                this.goodsList = []
                this.getGoods()
            }
        })
    },
    components: {
        NiceTitle,
        Sort,
        Tab,
        Goods,
        InfiniteLoading,
        TabBar
    },

    methods: {
        infiniteHandler: function ($state) {
          var that = this
          axios.get('/api/taobao/list?max_price=10&sort=' + this.sortnumber + '&cat=' + this.cat + '&page=' + this.page)
          .then(function (res) {
            let data = res.data
            if (data.er_code === 10000 && data.data.list) {
                that.page++
                if (that.page === 1) {
                    that.goodsList = data.data.list
                } else {
                    that.goodsList.push.apply(that.goodsList, data.data.list)
                }
                that.total = data.data.total
                that.page * 100 < that.total ? (that.nodata = false) : (that.nodata = true)

                if (that.nodata) {
                  $state.complete()
                } else {
                  $state.loaded()
                }
            }
          })
        }
    },
    mounted () {

    }
}
</script>

<style scoped lang="stylus">
    .page
        .scroll
            position: absolute
            width: 100%
            top: 1.55rem
            left: 0
            bottom: 1rem
            z-index: -1
        .bottom-tip
            height: .8rem
            line-height: .8rem
            text-align: center
            font-size: .2rem
            color: #888
            position: absolute
            bottom: 1rem
            width: 100%
            z-index: -2
</style>



