<template>
    <div class="search-list">
        <cat-head class="header" :title="title"></cat-head>
        <div class="sort-wrapper">
            <sort class="sort"></sort>
        </div>
        <div class="wrapper" ref="wrapper">
            <goods :goodslist="goodsList"></goods>
            <infinite-loading :identifier="infiniteId" @infinite="infiniteHandler"></infinite-loading>
        </div>
        <div class="bottom-tip" v-show="nodata">暂无数据</div>
    </div>
</template>

<script type="text/ecmascript-6">
import axios from 'axios'
import Goods from 'common/goods/Goods'
import CatHead from 'common/head/Head'
import InfiniteLoading from 'vue-infinite-loading'
import Sort from 'common/sort/Sort'
import Bus from '@/assets/js/eventBus'
export default {
    name: 'SearchList',
    data () {
        return {
            key: '',
            page: 1,
            goodsList: [],
            total: '',
            title: '',
            sortnumber: 1,
            cat: 0,
            nodata: false,
            infiniteId: +new Date()
        }
    },
    created () {
        // this.key = this.$route.params.key
        // this.title = this.$route.params.title
        // console.log(this.title)
        // console.log(this.key)
        // console.log('created')
        // this.getSearchList(this.key)
        Bus.$on('sortnumber', sortnumber => {
            if (sortnumber !== this.sortnumber) {
                this.sortnumber = sortnumber
                this.page = 1
                this.nodata = false
                this.goodsList = []
                this.infiniteId += 1
            }
        })
    },
    components: {
        Goods,
        Sort,
        CatHead,
        InfiniteLoading
    },
    methods: {
        infiniteHandler: function ($state) {
          var that = this
          axios.get('/api/taobao/list?sort=' + this.sortnumber + '&cat=' + this.cat + '&page=' + this.page)
          .then(function (res) {
            let data = res.data
            if (data.er_code === 10000 && data.data.list) {
                that.page++
                if (that.page === 1) {
                    that.goodsList = data.data.list
                } else {
                    that.goodsList.push.apply(that.goodsList, data.data.list)
                }
                that.total = data.data.total
                that.page * 100 < that.total ? (that.nodata = false) : (that.nodata = true)

                if (that.nodata) {
                  $state.complete()
                } else {
                  $state.loaded()
                }
            }
          })
        }
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
        if (from.path === '/detail') {
            // console.log('返回的的')
            to.meta.isBack = true
        } else {
            // console.log('新进入的')
            vm.cat = vm.$route.params.cat
            vm.title = vm.$route.params.title
            vm.nodata = false
            vm.page = 1
            vm.goodsList = []
            vm.infiniteId += 1
        }
        })
    }
}
</script>

<style scoped lang="stylus">
    .header
        position: fixed
        top: 0
        left: 0
        right: 0
        z-index: 1000
    .sort-wrapper
        position: fixed
        top: .9rem
        left: 0
        right: 0
        z-index: 1000
    .wrapper
        position: absolute
        top: 1.65rem
        left: 0
        bottom: 0
        right: 0
    .bottom-tip
        height: .9rem
        line-height: .9rem
        text-align: center
        font-size: .2rem
        color: #888
        position: absolute
        bottom: 0
        width: 100%
        z-index: -1

</style>



